html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
footer,
header,
nav,
section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

figcaption,
figure,
main {
  display: block;
}

figure {
  margin: 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details,
menu {
  display: block;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

@keyframes upDown {
  0% {
    transform: translateY(-5rem);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-5rem);
  }
}
@keyframes up {
  0% {
    opacity: 0;
    transform: translateY(-5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0rem);
  }
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.8rem;
  font-weight: 600;
  color: #23303e;
  line-height: 1.7;
}

.container {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100vh repeat(3, 70vh) min-content 70vh max-content;
}
@media only screen and (max-width: 52.5em) {
  .container {
    grid-template-rows: 80vh repeat(5, min-content) max-content;
  }
}

.heading {
  font-family: "Fraunces", serif;
  line-height: 1.2;
}
.heading--primary {
  text-transform: uppercase;
  color: #ffffff;
  animation: up 2s ease-in;
  font-size: clamp(3.8rem, 5vw, 6rem);
  letter-spacing: 1rem;
}
.heading--secondary {
  font-size: clamp(3rem, 5vw, 4.5rem);
  font-weight: 900;
}
.heading--third {
  font-size: 3rem;
}
.heading--testimonials {
  text-transform: uppercase;
  letter-spacing: 5px;
  color: #a7abae;
  font-size: 1.8rem;
}

.paragraph {
  color: #818498;
}

.mt-m {
  margin-top: 4rem !important;
}

.btn {
  text-transform: uppercase;
  font-family: "Fraunces", serif;
}
.btn--primary {
  background-color: #ffffff;
  padding: 1.8rem 2.5rem;
  border-radius: 5rem;
  color: #23303e;
  transition: all 0.3s;
}
.btn--primary:hover {
  background-color: rgba(255, 255, 255, 0.3);
  color: #ffffff;
}
@media only screen and (max-width: 52.5em) {
  .btn--primary {
    background-color: #fad400;
    color: #23303e;
  }
}
.btn--secondary {
  font-size: 1.5rem;
  text-decoration: none;
  color: #23303e;
  position: relative;
  width: max-content;
}
.btn--secondary::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 50%;
  background-color: rgba(250, 212, 0, 0.3);
  z-index: -1;
  transition: all 0.3s;
  border-radius: 1rem;
}
.btn--audience::after {
  background-color: rgba(254, 120, 103, 0.3);
}
.btn--audience:hover::after {
  background-color: #fe7867;
}
.btn--brand::after {
  background-color: rgba(250, 212, 0, 0.3);
}
.btn--brand:hover::after {
  background-color: #fad400;
}

.info {
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  color: #19536b;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  width: 50%;
}
@media only screen and (max-width: 72.8125em) {
  .info {
    width: 80%;
  }
}
@media only screen and (max-width: 65em) {
  .info {
    width: 90%;
  }
}

.logo--header {
  height: 3rem;
}
@media only screen and (max-width: 52.5em) {
  .logo--header {
    height: 2.5rem;
  }
}
.logo--footer {
  height: 3rem;
  filter: invert(51%) sepia(22%) saturate(736%) hue-rotate(118deg) brightness(90%) contrast(92%);
}

.review {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
  width: 35rem;
}
@media only screen and (max-width: 52.5em) {
  .review {
    gap: 3rem;
  }
}
.review__box {
  overflow: hidden;
  width: 7rem;
  height: 7rem;
  border-radius: 100%;
}
.review__box:hover {
  backdrop-filter: blur(50%);
}
.review__photo {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.review__author {
  display: flex;
  flex-direction: column;
}
.review__name {
  font-family: "Fraunces", serif;
  font-size: 2rem;
}
.review__job {
  color: #a7abae;
}

.social {
  display: flex;
  gap: 2.5rem;
}
.social__icon {
  cursor: pointer;
  transition: all 0.4s;
}
.social__icon:hover {
  filter: brightness(0) invert(1);
}

.testimonials {
  padding: 15rem 0rem;
  display: flex;
  flex-direction: column;
  gap: 6rem;
  align-items: center;
  text-align: center;
}
.testimonials__content {
  display: flex;
  width: 75vw;
  gap: 3rem;
  justify-content: space-between;
}
@media only screen and (max-width: 52.5em) {
  .testimonials__content {
    flex-direction: column;
    align-items: center;
    gap: 6rem;
  }
}
@media only screen and (max-width: 52.5em) {
  .testimonials {
    padding: 7rem 0rem;
  }
}

.footer {
  background-color: #8ad2c4;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  padding: 5.5rem 2rem 9rem 2rem;
}
.footer__link {
  color: #458c7e;
  text-decoration: none;
  transition: all 0.3s;
}
.footer__link:hover {
  color: #ffffff;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  height: 11rem;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 2rem 3rem;
  z-index: 300;
  transition: all 0.3s;
}
.header .left {
  flex: 1;
}
.header__toggle {
  position: absolute;
  right: 4.5%;
  cursor: pointer;
  display: none;
}
@media only screen and (max-width: 52.5em) {
  .header__toggle {
    display: block;
  }
}

.sticky-nav {
  height: 8rem;
  position: fixed;
  background-color: rgba(69, 140, 126, 0.7);
  box-shadow: rgba(37, 86, 75, 0.4) 0px 7px 29px 0px;
}

.navigation {
  z-index: 200;
}
.navigation__list {
  display: flex;
  list-style: none;
  gap: 4rem;
}
.navigation__link {
  text-decoration: none;
}
.navigation__link:not(.btn--primary, .footer__link) {
  color: #ffffff;
}
@media only screen and (max-width: 52.5em) {
  .navigation {
    position: absolute;
    top: 13rem;
    left: 50%;
    transform: translateX(-50%);
    background-color: #ffffff;
    width: 90%;
    transition: all 0.2s;
    height: 0;
  }
  .navigation__list:not(.footer__list) {
    flex-direction: column;
    align-items: center;
    gap: 2.5rem;
    padding: 3rem;
    padding-bottom: 4.5rem;
    box-shadow: rgba(37, 86, 75, 0.4) 0px 7px 29px 0px;
    transition: all 0.2s;
    display: none;
  }
  .navigation__list:not(.footer__list)::before {
    content: "";
    position: absolute;
    right: 0;
    top: -12%;
    height: 5rem;
    width: 5rem;
    clip-path: polygon(100% 0, 0% 100%, 100% 100%);
    background-color: #ffffff;
  }
  .navigation__link {
    font-weight: 900;
  }
  .navigation__link:not(.btn--primary, .footer__link) {
    color: #818498;
  }
}

.show-nav {
  height: max-content;
  transition: all 0.2s;
}

.show-menu:not(.footer__list) {
  display: flex;
}

.hero {
  background: url(image-header.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero__content {
  position: absolute;
  width: 100%;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  text-align: center;
}
.hero__arrow {
  width: 3.7rem;
  cursor: pointer;
  animation: upDown 2s infinite;
}
@media only screen and (max-width: 65em) {
  .hero__arrow {
    width: 3rem;
  }
}

.section--main {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 100%;
}
@media only screen and (max-width: 52.5em) {
  .section--main {
    grid-template-columns: 100%;
    grid-template-rows: 40vh 60vh;
    text-align: center;
  }
}
.section--description {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 100%;
}
@media only screen and (max-width: 52.5em) {
  .section--description {
    grid-template-columns: 100%;
    grid-template-rows: repeat(2, 80vh);
    text-align: center;
  }
}
.section--images {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 100%;
}
@media only screen and (max-width: 52.5em) {
  .section--images {
    height: 100%;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
.section__content {
  padding: 8vw 9vw;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: center;
}
@media only screen and (max-width: 52.5em) {
  .section__content {
    align-items: center;
  }
  .section__content--change {
    grid-row: 2/-1;
  }
}
.section__resource {
  width: 100%;
}
.section__resource--info {
  position: relative;
}
.section__photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}